import { css, html } from 'lit'
import {customElement} from 'lit/decorators.js'
import { IconName } from './icons'
import { ToolbarItemWithChildrens } from './ToolbarItemWithChildrens'

@customElement('ui-new-note-toolbar-item')
export class NewNoteToolbarItem extends ToolbarItemWithChildrens {
	static styles = [
		...super.styles,
		css`
			.color-item {
				height: var(--element-size-unit);
				width: var(--element-size-unit);
				border-radius: 50%;
				cursor: pointer;
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
			}

			.color-item:hover {
				background: var(--background-secondary);
			}
			.color {
				height: calc(var(--spacing-unit) * 3);
				width: calc(var(--spacing-unit) * 3);
				background: red;
				border-radius: 50%;
				border: 2px solid #2a2b2b;
				cursor: pointer;
			}
			.color-item:hover .color {
				border-color: #025E73;
			}
		`
	]
	icon : IconName = 'note'

	constructor () {
		super()
	}

	// eslint-disable-next-line @typescript-eslint/no-empty-function
	clickHandler(): void {}

	subitems () {
		const colors = [
			'#fd8111', // event
			'#68B521', // view-model
			'#12AAFC', // command
			'#FCD512', // actor
			'#FC12D1', // policy
			'#FC1245', // hotspot
			'#EEBF59', // aggregate
		]
		return html`
			${colors.map(color => html`
				<div
					class="color-item"
					@click="${this.colorClickHandler(color)}"
				>
					<div
						class="color"
						style="background: ${color};"
					></div>
				</div>
			`)}
		`
	}

	private colorClickHandler (color: string) {
		return () => {
			this.closeSubitems()
			this.app.createNoteOnCanvasClickedPoint(color)
		}
	}
}
